.box5 {
	width: 360px;
	height: 390px;
	.quote {
		font-style: italic;
		line-height: 1.5;
		text-align: center;
		padding: 30px 35px;
	}
	.quotee {
		// padding-top: 40px;
		text-align: center;
	}
}

.box5 p.quote::after {
	content: " ";
   display: block;
	border-bottom: 1px solid #fff;
   width: 50px;
   margin: 20px auto;
   padding-top: 5%;
}


.reviewsGallery {
	color: #fff;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-around;
	li {
		width: 31%;
		margin-bottom: 25px;
		box-shadow: 0 5px 15px 2px rgba(0,0,0,.2);
	}
}

