section.discount {
	background: url(../assets/discount-banner.png) center center no-repeat;
	padding: 30px;
	margin: 30px 0;
	h2 {
		letter-spacing: 1px;
		color: #fff;
		&::after {
			border-bottom: 1px solid #fff;
		}
	}
	form {
		text-align: center;
	}
	form input[type=submit] {
		border: none;
		border-radius: 2px;
		padding: 10px 25px;
		box-shadow: 0px 1px 6px 2px rgba(0, 0, 0, 0.2);
		width: 180px;
		background: $darkyellow;
		color: #fff;		
	}
}

section.discount form input[type=email] {
	color: #fff;
	@include fontSize(14, 400)
	width: 30%;
	background: none;
	padding: 10px 0;
	margin-right: 20px;
	border-top: 0;
	border-left: 0;
	border-right: 0;
	border-bottom: 1px solid white;
}